.plancalc__ {
           background-color: #ffc107;
           display: flex;
           width:30%;
           padding: 10px;
           flex-direction: column;
           justify-content: space-evenly;
           align-items: center;
           height: 200px;
           border-radius: 10px;
           margin: 50px 0 0 60px;
}

.plancalc__ button{
           border: solid thin white;
           padding: 5px;
           background-color: transparent;
           color: white;
           outline: none;
           width: 200px;
           transition: 0.3s ease-in-out;
}

.plancalc__ button:hover {
           color: #ffc107;
           background-color: white;
}


.plancalc__ h2 {
           color: white;
}