.body {
  text-align: center;
  align-items: center;
  display: flex;
  width: 100%;
  justify-content:center;
  height: 80vh;
}
.addcostumer__inputs {
  display: flex;
  width: 20%;
  box-shadow: -7.5px 10px 50px #2a137a8e;
  flex-direction: column;
  padding: 10px;
}
.addcostumer__inputs input {
  border-top: none;
  border-left: none;
  border-right: none;
  border-width: thin;
  margin-top: 10px;
  outline: none;
}


@media only screen and (max-width:1018px) {
    .addcostumer__inputs {
      width: 30%;
    }
}

@media only screen and (max-width:463px) {
  .addcostumer__inputs {
    width: 100%;
  }
}