.loginContainer {
	width: 100%;
	height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.loginForm {
	display: flex;
	flex-direction: column;
	padding: 30px 40px;
	justify-content: space-evenly;
	height: 70%;
	background-color: rgba(245, 245, 245, 0.829);
	border-radius: 20px;
	box-shadow: 0px 0px 10px #4627b48e;
}

.loginForm input {
	background-color: transparent;
	font-weight: 600;
}

.loginForm input::placeholder {
	color: rgba(0, 0, 0, 0.479);
}

.button1 {
	margin-top: 4px;
	border: 1px solid;
	padding: 4px;
	background-color: grey;
}
