.Link {
	border: solid thin black;
	padding: 5px;
	color: black;
	background-color: transparent;
	margin-left: 20px;
}

.Link:hover {
	text-decoration: none;
	color: white;
	background-color: black;
	transform: translate(221);
}

.top____ {
	padding: 3px;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin: 10px;
}

.top____ h3 {
	font-size: 25px;
	color: blueviolet;
}

.button____ {
	padding: 10px;
	border: solid thin;
	margin: 10px;
	background-color: transparent;
	color: purple;
	transition: 0.5s ease-in-out;
}

.button____:hover {
	background-color: purple;
	color: white;
}

.late {
	width: 100px;
	font-weight: bolder;
	color: red;
}

.normal {
	width: 100px;
	font-weight: bolder;
}

.viewcostumers_tb_tb {
	border-top: none;
	border-right: none;
	border-left: none;
	border-bottom: solid 1px black;
	margin-top: 30px;
	padding: 10px;
	width: 20%;
	cursor: pointer;
	border-radius: 20px;
	background: linear-gradient(#fff 0%, #fef4f4 100%);
	box-shadow: -5px 3px 20px rgba(0, 0, 0, 0.16);
	transition: 0.4s;
	margin-right: 50px;
}

.viewcostumers_tb_tb:hover {
	border-radius: 10px;
	color: blueviolet;
	box-shadow: -20px 10px 50px rgba(0, 0, 0, 0.16);
	padding: 20px;
	border-bottom: 2px solid black;
}

.viewcostumers_tb {
	flex-wrap: wrap;
	flex-direction: row;
	width: 100%;
	display: flex;
}

.viewcostumers_table {
	width: 100%;
}

@media only screen and (max-width: 877px) {
	.viewcostumers_tb_tb {
		margin-right: 20px;
		width: auto;
	}
	.viewcostumers_tb {
		display: flex;
		justify-content: center;
	}
}

.viewcostumers_table {
	user-select: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}
