.addCollection__dateInput {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.addCollection__dateInput p {
  font-size: 20px;
}

.addCollection_ {
  display: flex;
  flex-direction: column;
}


.addCollection__formcollection {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}

.addCollection__collectionTable {
  overflow-y: auto;
  height: 490px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.addCollection__collectionTableBody {
  display:flex;
  flex-wrap: wrap;
  margin-top: 10px;
  justify-content: space-between;
  border: solid thin black;
  align-items:center;
}

.addCollection__collectionTableBody p:nth-child(1) {
  width: 30%;
  margin:10px;
}

.addCollection__collectionTableBody p:nth-child(2) {
  margin: 10px;
}

.addCollection__collectionTableBody p:nth-child(3) {
  margin: 10px;
}

.addCollection__collectionTableBody p:nth-child(4) {
  margin: 10px;
}

.ammounts {
  padding:  0 20px 0 20px;
  margin:  10px;
  border:  solid 1px black;
  width: auto
}

.loadingstate {
  border:none;
  padding: 20px;
  font-size:30px
}

.late {
  color: red;
}

