.class_nav_bar {
	padding: 10px;
	margin: 0 20px 0 20px;
	color: white;
	background-color: black;
	transition: 0.4s ease-in-out;
}
.navbar {
	background-color: black;
}
.App__companyName {
	color: rebeccapurple;
}
.class_nav_bar:hover {
	color: black;
	font-weight: bold;
	text-decoration: none;
	padding: 15px;
	background-color: rosybrown;
}
.App__mainContent {
	margin-top: 20px;
	padding: 5px;
	height: 100%;
	width: auto;
	align-items: center;
	padding: 0 40px 0 40px;
}
/*

.App {
   user-select: none; 
   -webkit-user-select: none;
   -khtml-user-select: none;
   -moz-user-select: none; 
   -ms-user-select: none; 
}
*/

@media (max-width: 1204px) {
	.class_nav_bar {
		display: flex;
	}
	.App__companyName {
		display: none;
	}
	.nav_bar {
		width: 100%;
	}
}
