.reports__top {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	width: 100%;
	justify-content: space-around;
}

.reports__header {
	display: flex;
	flex-direction: row;
	width: 20%;
	justify-content: space-between;
}

.reports__expence {
	display: flex;
	flex-direction: column;
	margin: 30px;
}

.reports__expence select,
input {
	border: solid thin;
	outline: none;
}

.reports__expence button {
	border: solid thin #5b0dda;
	background-color: #43108f;
	color: white;
	padding: 7px;
	text-align: center;
	font-weight: bold;
}

.reports__expence button:hover {
	border: solid thin #5b0dda;
	background-color: transparent;
	color: #5b0dda;
}

.button1 {
	border: solid thin #5b0dda;
	background-color: #43108f;
	color: white;
	padding: 5px;
	font-weight: bold;
	text-align: center;
}

.button1:hover {
	border: solid thin #5b0dda;
	background-color: transparent;
	color: #5b0dda;
}

.reports__bottom2 {
	font-family: Georgia, "Times New Roman", Times, serif;
	color: darkblue;
	margin: 30px 0 0 0;
}

.reports__plan {
	flex: 0.5;
}
